import { SyntheticEvent, useEffect, useMemo, useState } from 'react';
import ReCAPTCHA from 'react-google-recaptcha';
import useToast from '../Hooks/useToast';

export default function Contact() {
  
  function handleChange(e: SyntheticEvent) {
    const input = e.target as HTMLInputElement;
    setEmail(prev => ({ ...prev, [input.id]: input.value }));
  }

  function handleCaptcha(token: string | null) {
    if (token) {
      setVerified(true);
    }
    setCaptcha(false);
  }

  function handleSubmit(e: SyntheticEvent) {
    e.preventDefault();
    setCaptcha(true);
  }

  const
  initialData = useMemo(() => ({
    name: '',
    from: '',
    subject: '',
    text: ''
  }), []),
  toast = useToast(),
  [email, setEmail] = useState(initialData),
  [captcha, setCaptcha] = useState(false),
  [verified, setVerified] = useState(false);

  useEffect(() => {

    const secret = [116, 97, 110, 49, 52, 49, 52, 50, 64, 103, 109, 97, 105, 108, 46, 99, 111, 109]
      .map(val => {
        return String.fromCharCode(val);
      })
      .join('');
    
    if (verified) {
      window.location.href = `mailto:${secret}?subject=${email.subject}&body=${email.text}%0D%0ARegards%0D%0A${email.name}`;
    }
  }, [email, initialData, toast, verified]);

  return (
    <>
      {captcha && (
        <div className="center-content">
          <ReCAPTCHA
          onChange={handleCaptcha}
          sitekey="6Ld3ddoZAAAAADjAJt1rOZwh1DdxdWGVs16Rc4Am"
          />
        </div>
      )}
      <section
      className={captcha ? "hide" : "accent-bottom center-content panel"}>
        <h2>Email Me</h2>
        <form
        onSubmit={handleSubmit}
        style={{ marginTop: "2rem" }}>
          <div className="input-field">
            <input
              id="name"
              onChange={handleChange}
              required
              value={email.name}
              type="text"
            />
            <label htmlFor="name">Your Name</label>
          </div>
          <div className="input-field">
            <input
              id="from"
              onChange={handleChange}
              required
              value = {email.from}
              type="email"
            />
            <label htmlFor="from">Your Email</label>
          </div>
          <div className="input-field">
            <input
              id="subject"
              onChange={handleChange}
              required
              value={email.subject}
              type="text"
            />
            <label htmlFor="subject">Subject</label>
          </div>
          <div className="input-field">
            <label htmlFor="text">Text</label>
            <textarea
            id="text"
            className="materialize-textarea"
            onChange={handleChange}
            required
            value={email.text} />
          </div>
          <div className="input-field">
            <button
            className="btn waves-effect waves-light"
            type="submit"
            name="action">
              Submit
              <i className="material-icons right">send</i>
            </button>
          </div>
        </form>
      </section>
    </>
  );
};